import { PencilIcon, PlusIcon, TrashIcon } from '@heroicons/react/24/outline';
import Link from 'next/link';
import { deleteMenu } from '@/app/lib/actions';

export function CreateProduk() {
  return (
    <Link
      href="/admin/dashboard/menu/create"
      className="flex h-10 items-center rounded-lg bg-red-600 px-4 text-sm font-medium text-white transition-colors hover:bg-red-500 focus-visible:outline-offset-2 focus-visible:outline-red-600"
    >
      <span className="hidden md:block">Create Produk</span>{' '}
      <PlusIcon className="h-5 md:ml-4" />
    </Link>
  );
}

export function UpdateProduk({ id }: { id: string }) {
  return (
    <Link
      href={`/admin/dashboard/menu/${id}/edit`}
      className="rounded-md border p-2 hover:bg-gray-100"
    >
      <PencilIcon className="w-5" />
    </Link>
  );
}

export function DeleteProduk({ id }: { id: string }) {
  const deleteMenuWithId = deleteMenu.bind(null, id);
  
  return (
    <form action={deleteMenuWithId}>
      <button 
        className="rounded-md border p-2 hover:bg-gray-100"
        onClick={(e) => {
          if (!confirm('Apakah Anda yakin ingin menghapus produk ini?')) {
            e.preventDefault();
          }
        }}
      >
        <span className="sr-only">Delete</span>
        <TrashIcon className="w-5" />
      </button>
    </form>
  );
}