'use client'

import { fetchAllProduk } from '@/app/lib/data';
import { alegreya } from '@/app/ui/fonts';
import { UpdateProduk, DeleteProduk } from '@/app/ui/menu/buttons';

export default async function ProdukTable({
  searchParams,
}: {
  searchParams: { query?: string; page?: string };
}) {
  const query = searchParams.query?.toLowerCase() || '';
  const currentPage = parseInt(searchParams.page || '1', 10);
  const itemsPerPage = 5;

  const produkList = await fetchAllProduk();

  const filtered = produkList.filter((produk) =>
    produk.nama_produk.toLowerCase().includes(query)
  );

  const totalItems = filtered.length;
  const totalPages = Math.ceil(totalItems / itemsPerPage);
  const paginated = filtered.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  return (
    <>
      <div className="mt-6 flow-root">
        <div className="inline-block min-w-full align-middle">
          <div className="rounded-lg bg-gray-50 p-2 md:pt-0">
            {/* Mobile view */}
            <div className="md:hidden">
              {paginated.map((produk) => (
                <div
                  key={produk.id_produk}
                  className="mb-2 w-full rounded-md bg-white p-4"
                >
                  <div className="flex items-center justify-between border-b pb-4">
                    <div>
                      <div className="mb-2 flex items-center">
                        <p className="text-lg font-medium">{produk.nama_produk}</p>
                      </div>
                      <p className="text-sm text-gray-500">ID: {produk.id_produk}</p>
                    </div>
                  </div>
                  <div className="flex w-full items-center justify-between pt-4">
                    <div>
                      <p className="text-xl font-medium">
                        Rp {produk.harga_produk.toLocaleString()}
                      </p>
                    </div>
                    <div className="flex justify-end gap-2">
                      <UpdateProduk id={produk.id_produk.toString()} />
                      <DeleteProduk id={produk.id_produk.toString()} />
                    </div>
                  </div>
                </div>
              ))}
            </div>

            {/* Desktop table view */}
            <table className="hidden min-w-full text-gray-900 md:table">
              <thead className="rounded-lg text-left text-sm font-normal">
                <tr>
                  <th scope="col" className={`${alegreya.className} px-4 py-5 font-medium sm:pl-6`}>
                    ID Produk
                  </th>
                  <th scope="col" className={`${alegreya.className} px-3 py-5 font-medium`}>
                    Nama Produk
                  </th>
                  <th scope="col" className={`${alegreya.className} px-3 py-5 font-medium`}>
                    Harga
                  </th>
                  <th scope="col" className="relative py-3 pl-6 pr-3">
                    <span className="sr-only">Actions</span>
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white">
                {paginated.map((produk) => (
                  <tr
                    key={produk.id_produk}
                    className="w-full border-b py-3 text-sm last-of-type:border-none [&:first-child>td:first-child]:rounded-tl-lg [&:first-child>td:last-child]:rounded-tr-lg [&:last-child>td:first-child]:rounded-bl-lg [&:last-child>td:last-child]:rounded-br-lg"
                  >
                    <td className="whitespace-nowrap py-3 pl-6 pr-3">
                      {produk.id_produk}
                    </td>
                    <td className="whitespace-nowrap px-3 py-3">
                      {produk.nama_produk}
                    </td>
                    <td className="whitespace-nowrap px-3 py-3">
                      Rp {produk.harga_produk.toLocaleString()}
                    </td>
                    <td className="whitespace-nowrap py-3 pl-6 pr-3">
                      <div className="flex justify-end gap-3">
                        <UpdateProduk id={produk.id_produk.toString()} />
                        <DeleteProduk id={produk.id_produk.toString()} />
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>

      {/* Pagination */}
      <div className="mt-4 flex justify-center gap-2">
        {[...Array(totalPages)].map((_, i) => {
          const page = i + 1;
          const params = new URLSearchParams();

          Object.entries(searchParams).forEach(([key, value]) => {
            if (value && key !== 'page') params.set(key, value);
          });

          params.set('page', String(page));
          const href = `?${params.toString()}`;

          return (
            <a
              key={page}
              href={href}
              className={`rounded px-3 py-1 text-sm ${
                page === currentPage ? 'bg-red-900 text-white' : 'border'
              }`}
            >
              {page}
            </a>
          );
        })}
      </div>
    </>
  );
}